.content.ota__add_update .input__wrap .section__inner>ul>li {
  width: 100%;
}

.content.ota__add_update .input__wrap .section__inner>ul>li:nth-of-type(2) {
  margin-top: 24px;
}

.content.ota__add_update .input__wrap .section__inner>ul>li:nth-of-type(2) .version__input {
  display: flex;
  justify-content: space-between;
}

.content.ota__add_update .input__wrap .section__inner>ul>li:first-of-type select {
  width: 200px;
}

.content.ota__add_update .input__wrap .section__inner>ul>li:nth-of-type(2) .version__input input {
  width: 24%;
}

.content.ota__add_update .input__wrap .section__inner>ul>li:nth-of-type(3) dd {
  display: flex;
  justify-content: space-between;
}

.content.ota__add_update .input__wrap .section__inner>ul>li:nth-of-type(3) dd input {
  width: calc(100% - 100px);
}

.content.ota__add_update .input__wrap .section__inner>ul>li:nth-of-type(4) textarea {
  height: 96px;
}

.content.ota__add_update .input__wrap .section__inner>ul>li:nth-of-type(4) textarea:focus {
  border: 1px solid rgb(222, 222, 222);
}